	module.exports = {"data":{"markdownRemark":{"html":"<h2>Breadth First Search (BFS)</h2>\n<p>Breadth First Search is one of the most simple graph algorithms. It traverses the graph by first checking the current node and then expanding it by adding its successors to the next level. The process is repeated for all nodes in the current level before moving to the next level. If the solution is found the search stops.</p>\n<h3>Visualisation</h3>\n<p><img src=\"https://upload.wikimedia.org/wikipedia/commons/4/46/Animated_BFS.gif\"></p>\n<h3>Evaluation</h3>\n<p>Space Complexity: O(n)</p>\n<p>Worse Case Time Complexity: O(n)</p>\n<p>Breadth First Search is complete on a finite set of nodes and optimal if the cost of moving from one node to another is constant.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href='https://github.com/freecodecamp/guides/computer-science/data-structures/graphs/index.md' target='_blank' rel='nofollow'>Graphs</a></p>\n<p><a href='https://github.com/freecodecamp/guides/tree/master/src/pages/algorithms/graph-algorithms/depth-first-search/index.md' target='_blank' rel='nofollow'>Depth First Search (DFS)</a></p>","fields":{"slug":"/algorithms/graph-algorithms/breadth-first-search/"},"frontmatter":{"title":"Breadth First Search (BFS)"}}},"pathContext":{"slug":"/algorithms/graph-algorithms/breadth-first-search/"}}

